//    - Read.jsx

import React, { useContext } from "react";
import { sCon } from "../sCon";

function Read({ setMode, selRecord, totalCount, setPageNum, pgPgNum }) {
  // setMode -    setter
  // selRecord -  

  //    
  const selData = selRecord.current;
  // console.log(selData);

  //   API !!
  const myCon = useContext(sCon);
  // console.log("Read loginSts:", myCon.loginSts);

  // [   ] /////
  const deleteFn = () => {
    //   /////
    if (window.confirm(" ?")) {
      // ""  true  !
      // console.log('~!');

      // 1)    ////////////
      // 1-1)    
      let localData = localStorage.getItem("posts");

      // 1-2) JSON.parse()  
      localData = JSON.parse(localData);

      // 2)    idx()
      let currIdx = selData.id;
      // console.log("idx:", currIdx);

      // 3)     some() 
      //  idx  
      // find()  some()  boolean 
      //  find() some() return true  
      localData.some((v, i) => {
        if (v.id === currIdx) {
          //   : i  
          localData.splice(i, 1);

          //  true  
          return true;
        } /// if ///
      }); ////// some ///////

      // 4)    
      localStorage.setItem("posts", JSON.stringify(localData));

      // 5)   1 ////
      totalCount.current--;

      // 6)   
      setPageNum(1);

      // 7)    
      pgPgNum.current = 1;

      // 8)     
      setMode("L");
    } /// if :confirm true ///
  }; ///////// deleteFn  ////////////////

  //   ///////////////////
  return (
    <main className="cont">
      <h2 className="tit"> </h2>
      <table className="dtblview readone">
        <tbody>
          <tr>
            {/* <td>Name</td> */}
            <td className="name-type-box">
              <div className="selbox-name">
                <input
                  type="text"
                  className="name"
                  readOnly={true}
                  defaultValue={selData.user_name}
                  disabled
                />
              </div>
              <div className="selbox-write">
                <input
                  type="text"
                  id="sel-type"
                  className="sel-type"
                  readOnly={true}
                  defaultValue={selData.post_type}
                  disabled
                />
              </div>
            </td>
          </tr>
          {
            selData.post_type === 'review' &&
          <tr>
            <td className="rating">
              {[...Array(5)].map((_, i) => {
                if (i < Math.floor(selData.rating)) {
                  return (
                    <img key={i} src={process.env.PUBLIC_URL +"/images/common/rating.png"} alt="" />
                  );
                } else if (
                  i === Math.floor(selData.rating) &&
                  selData.rating % 1 >= 0.5
                ) {
                  return (
                    <img
                      key={i}
                      src={process.env.PUBLIC_URL +"/images/common/rating_half.png"}
                      alt=" "
                    />
                  );
                } else {
                  return (
                    <img
                      key={i}
                      src={process.env.PUBLIC_URL +"/images/common/rating_empty.png"}
                      alt=" "
                    />
                  );
                }
              })}
            </td>
          </tr>
          }
          <tr>
            {/* <td>Title</td> */}
            <td>
              <input
                type="text"
                className="subject"
                size="60"
                readOnly={true}
                defaultValue={selData.title}
                disabled
              />
            </td>
          </tr>
          <tr>
            {/* <td>Content</td> */}
            <td>
              <textarea
                className="content"
                cols="60"
                rows="10"
                readOnly={true}
                defaultValue={selData.content}
                disabled
              ></textarea>
            </td>
          </tr>
        </tbody>
      </table>
      <br />
      <table className="dtbl btngrp">
        <tbody>
          <tr>
            <td className="write-page-button">
              <button
                onClick={() => {
                  //  ('L') 
                  setMode("L");
                }}
              >
                
              </button>
              {
                //      
                //  
                myCon.isLoggedIn &&
                  JSON.parse(sessionStorage.getItem("users")).id ===
                    selData.user_id && (
                    <button
                      onClick={() => {
                        //  
                        setMode("M");
                      }}
                    >
                      
                    </button>
                  )
              }
              {myCon.isLoggedIn &&
                JSON.parse(sessionStorage.getItem("users")).id ===
                  selData.user_id && (
                  <button onClick={deleteFn}></button>
                )}
            </td>
          </tr>
        </tbody>
      </table>
    </main>
  );
}
export default Read;
