import React, { useState } from "react";

import { Link, useNavigate } from "react-router-dom";

//  CSS  /////
import "../../css/pages/member.scss";

//   ////
import $ from "jquery";

function SignUp() {
  const goPage = useNavigate();

  const [userEmail, setuserEmail] = useState("");
  const [password, setpassword] = useState("");
  const [chkPassword, setChkPassword] = useState("");
  const [userName, setUserName] = useState("");

  const [userEmailError, setuserEmailError] = useState(false);
  const [passwordError, setpasswordError] = useState(false);
  const [chkPasswordError, setChkPasswordError] = useState(false);
  const [userNameError, setUserNameError] = useState(false);

  // [    ] ////
  const msgId = [
    " 5  ",
    "  .",
    " !",
  ];

  // [    ]
  const msgEtc = {
    password: ", ,   5  ",
    confpassword: "  ",
    req: " ",
  }; ///// msgEtc ///////

  const [idMsg, setIdMsg] = useState(msgId[0]);

  // [    ] ///////
  const changeuserEmail = (e) => {
    let val = e.target.value;

    // 1.   
    const valid =
      /^(([^<>()\[\].,;:\s@"]+(\.[^<>()\[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;

    if (valid.test(val)) {
      let userData = localStorage.getItem("users");

      userData = JSON.parse(userData);

      let isT = userData.some((v) => v.email === val);

      if (isT) {
        setIdMsg(msgId[1]);
        setuserEmailError(true);
      } ///// if /////
      else {
        setuserEmailError(false);
      } ///// else //////
    } /// if /////////////////////////
    else {
      setIdMsg(msgId[0]);
      setuserEmailError(true);
    } /// else ///

    setuserEmail(val);
  }; ////////// changeuserEmail  ////////////

  // 2.    ///////////
  const changepassword = (e) => {
    let val = e.target.value;

    const valid = /^.*(?=^.{5,15}$)(?=.*\d)(?=.*[a-zA-Z])(?=.*[!@#$%^&+=]).*$/;

    if (valid.test(val)) setpasswordError(false);
    else setpasswordError(true);

    setpassword(val);
  }; ///////// changepassword  //////////

  // 3.    ///////////
  const changeChkPassword = (e) => {
    let val = e.target.value;

    if (password === val) setChkPasswordError(false);
    else setChkPasswordError(true);

    setChkPassword(val);
  }; ///////// changeChkPassword  //////////

  // 4.    ///////////
  const changeUserName = (e) => {
    let val = e.target.value;

    if (val !== "") setUserNameError(false);
    else setUserNameError(true);

    setUserName(val);
  }; ///////// changeUserName  //////////

  // [    ] ///////////
  const totalValid = () => {
    if (!userEmail) setuserEmailError(true);
    if (!password) setpasswordError(true);
    if (!chkPassword) setChkPasswordError(true);
    if (!userName) setUserNameError(true);

    if (
      userEmail &&
      password &&
      chkPassword &&
      userName &&
      !userEmailError &&
      !passwordError &&
      !chkPasswordError &&
      !userNameError
    )
      return true;
    else return false;
  }; /////////// totalValid  ///////////

  // [   ] ////////////////
  const onSubmit = (e) => {
    // 1.  
    e.preventDefault();

    // 2.   
    if (totalValid()) {

      let userData = localStorage.getItem("users");

      userData = JSON.parse(userData);

      let temp = userData.map((v) => v.id);

      let newData = {
        id: Math.max(...temp) + 1,
        email: userEmail,
        password: password,
        name: userName,
      };

      userData.push(newData);

      localStorage.setItem("users", JSON.stringify(userData));

      document.querySelector(".txt-box").innerText =
        " !";

      setTimeout(() => {
        goPage("/login");
      }, 1000);
    } ///////// if /////////
    else {
      alert(" !");
    } //// else ///////////
  }; /////////// onSubmit  //////////

  //   ///////////////
  return (
    <div className="outbx">
      <section className="membx">
        <h2 className="txt-box"></h2>
        <form action="process.php" method="post">
          <ul>
            <li>
              <label> </label>
              <input
                type="text"
                maxLength="20"
                placeholder=" "
                value={userEmail}
                onChange={changeuserEmail}
                onBlur={changeuserEmail}
              />
              {userEmailError && (
                <div className="msg">
                  <small
                    style={{
                      color: "red",
                      fontSize: "10px",
                    }}
                  >
                    {idMsg}
                  </small>
                </div>
              )}
              {!userEmailError && userEmail && (
                <div className="msg">
                  <small
                    style={{
                      color: "green",
                      fontSize: "10px",
                    }}
                  >
                    {msgId[2]}
                  </small>
                </div>
              )}
            </li>
            <li>
              <label> </label>
              <input
                type="password"
                maxLength="20"
                placeholder=" "
                value={password}
                onChange={changepassword}
                onBlur={changepassword}
              />
              {passwordError && (
                <div className="msg">
                  <small
                    style={{
                      color: "red",
                      fontSize: "10px",
                    }}
                  >
                    {msgEtc.password}
                  </small>
                </div>
              )}
            </li>
            <li>
              <label>  </label>
              <input
                type="password"
                maxLength="20"
                placeholder="  "
                value={chkPassword}
                onChange={changeChkPassword}
                onBlur={changeChkPassword}
              />
              {chkPasswordError && (
                <div className="msg">
                  <small
                    style={{
                      color: "red",
                      fontSize: "10px",
                    }}
                  >
                    {msgEtc.confpassword}
                  </small>
                </div>
              )}
            </li>
            <li>
              <label> </label>
              <input
                type="text"
                maxLength="20"
                placeholder=" "
                value={userName}
                onChange={changeUserName}
                onBlur={changeUserName}
              />
              {userNameError && (
                <div className="msg">
                  <small
                    style={{
                      color: "red",
                      fontSize: "10px",
                    }}
                  >
                    {msgEtc.req}
                  </small>
                </div>
              )}
            </li>

            <li className="center">
                ?
              <Link to="/login"></Link>
            </li>
            <li className="center" style={{ overflow: "hidden" }}>
              <button className="lbtn" onClick={onSubmit}>
                
              </button>
            </li>
          </ul>
        </form>
      </section>
    </div>
  );
}
export default SignUp;
