// [   ] /////////
const clearPostData = () => {
  localStorage.removeItem("posts");
  console.log("  ");
}; /////////// clearData //////////////

// [  ! ] ////////////
const initPostData = () => {
  //   "mem-data" null !
  if (localStorage.getItem("posts") === null) {
    localStorage.setItem("posts", JSON.stringify(posts));
  }
}; ///////////// initData /////////////////

export { clearPostData, initPostData };
