import { Link, useNavigate } from "react-router-dom";
import { menu } from "../../js/data/gnb"; // GNB  
import "../../css/common/top_area.scss"; // CSS 
import { useState, useEffect } from "react";

export default function TopArea({ isLoggedIn, setIsLoggedIn }) {
  const [isOpen, setIsOpen] = useState(false); //    
  const navigate = useNavigate();

  //      
  useEffect(() => {
    const userData = sessionStorage.getItem("users");
    setIsLoggedIn(!!userData); //   true,  false
  }, [setIsLoggedIn]); // setIsLoggedIn   

  const toggleMenu = () => {
    setIsOpen(!isOpen); //   / 
  };

  const handleLogout = () => {
    sessionStorage.clear(); //    
    setIsLoggedIn(false); //  
    navigate("/"); //     
  };

  return (
    <>
      <header id="header">
        <div className="top-area">
          {/*  */}
          <div className="logo">
            <Link to="/">
              <img src={process.env.PUBLIC_URL +"/images/logo/logo.png"} alt="Logo" />
            </Link>
          </div>

          {/*  GNB */}
          <div className="header-nav">
            {isLoggedIn ? (
              <ul>
                {menu.slice(0, 4).map((v, i) => (
                  <li key={i}>
                    {v.sub ? (
                      <a href="#" onClick={(e) => e.preventDefault()}>
                        {v.txt}
                      </a>
                    ) : (
                      <Link to={v.link}>{v.txt}</Link>
                    )}
                    {v.sub && (
                      <div className="smenu">
                        <ol>
                          {v.sub.map((subItem, j) => (
                            <li key={j}>
                              <Link
                                to={subItem.link}
                                state={{ local: subItem.local }}
                              >
                                {subItem.txt}
                              </Link>
                            </li>
                          ))}
                        </ol>
                      </div>
                    )}
                  </li>
                ))}
              </ul>
            ) : (
              <ul>
                {menu.slice(0, 3).map((v, i) => (
                  <li key={i}>
                    {v.sub ? (
                      <a href="#" onClick={(e) => e.preventDefault()}>
                        {v.txt}
                      </a>
                    ) : (
                      <Link to={v.link}>{v.txt}</Link>
                    )}
                    {v.sub && (
                      <div className="smenu">
                        <ol>
                          {v.sub.map((subItem, j) => (
                            <li key={j}>
                              <Link
                                to={subItem.link}
                                state={{ local: subItem.local }}
                              >
                                {subItem.txt}
                              </Link>
                            </li>
                          ))}
                        </ol>
                      </div>
                    )}
                  </li>
                ))}
              </ul>
            )}
          </div>

          {/*      */}
          <div className="log-wrap">
            {isLoggedIn ? (
              <ul>
                <li>
                  <span>{JSON.parse(sessionStorage.getItem("users")).name}</span>
                </li>
                <li>
                  <a href="#" onClick={handleLogout} className="logout-btn">
                    
                  </a>
                </li>
              </ul>
            ) : (
              <ul>
                {menu.slice(4).map((v, i) => (
                  <li key={i}>
                    {v.sub ? (
                      <a href="#" onClick={(e) => e.preventDefault()}>
                        {v.txt}
                      </a>
                    ) : (
                      <Link to={v.link}>{v.txt}</Link>
                    )}
                  </li>
                ))}
              </ul>
            )}
          </div>

          {/*    */}
          <div
            className={`hambtn ${isOpen ? "open" : ""}`}
            onClick={toggleMenu}
          >
            <div className="hammenu">
              <div className="ham">
                <span></span>
                <span></span>
                <span></span>
              </div>
            </div>
            <div className="ham-drop-box">
              {isLoggedIn ? (
                <ul className="ham-drop">
                  {menu.slice(0, 4).map((v, i) => (
                    <li key={i}>
                      {v.sub ? (
                        <a href="#" onClick={(e) => e.preventDefault()}>
                          {v.txt}
                        </a>
                      ) : (
                        <Link to={v.link}>{v.txt}</Link>
                      )}
                      {v.sub && (
                        <div className="smenu">
                          <ol>
                            {v.sub.map((subItem, j) => (
                              <li key={j}>
                                <Link
                                  to={subItem.link}
                                  state={{ local: subItem.local }}
                                >
                                  {subItem.txt}
                                </Link>
                              </li>
                            ))}
                          </ol>
                        </div>
                      )}
                    </li>
                  ))}
                </ul>
              ) : (
                <ul className="ham-drop">
                  {menu.slice(0, 3).map((v, i) => (
                    <li key={i}>
                      {v.sub ? (
                        <a href="#" onClick={(e) => e.preventDefault()}>
                          {v.txt}
                        </a>
                      ) : (
                        <Link to={v.link}>{v.txt}</Link>
                      )}
                      {v.sub && (
                        <div className="smenu">
                          <ol>
                            {v.sub.map((subItem, j) => (
                              <li key={j}>
                                <Link
                                  to={subItem.link}
                                  state={{ local: subItem.local }}
                                >
                                  {subItem.txt}
                                </Link>
                              </li>
                            ))}
                          </ol>
                        </div>
                      )}
                    </li>
                  ))}
                </ul>
              )}
            </div>
          </div>
        </div>
      </header>
    </>
  );
}
