import React from "react";
import "../../css/modules/reserv_room.scss";

function ReservRoom({
  availableRooms,
  stayDuration,
  checkIn,
  checkOut,
  guestCount,
}) {
  const handleReservation = (room) => {
    const user = JSON.parse(sessionStorage.getItem("users"));
    const user_id = user ? user.id : null;

    if (!user_id) {
      alert("  .");
      return;
    }

    //    
    const confirmReservation = window.confirm("?");

    if (!confirmReservation) {
      return;
    }

    //     
    const existingReservations =
      JSON.parse(localStorage.getItem("reservations")) || [];
    const isRoomReserved = existingReservations.some(
      (reservation) =>
        reservation.user_id === user_id &&
        reservation.room_id === room.id &&
        reservation.check_in === checkIn
    );

    if (isRoomReserved) {
      alert("  .");
      return;
    }

    //    
    const reservation = {
      id: `${room.id}-${checkIn}`,
      user_id: user_id,
      room_id: room.id,
      check_in: checkIn,
      check_out: checkOut,
      guest_count: guestCount,
    };

    existingReservations.push(reservation);
    localStorage.setItem("reservations", JSON.stringify(existingReservations));

    alert(".");
  };

  return (
    <div>
      <ul>
        {availableRooms.length > 0 ? (
          availableRooms.map((room) => (
            <li className="roominfo-card" key={room.id}>
              <div className="inner-con">
                <p>
                  {room.room_type}({room.base_guests})
                </p>
                <p>{room.room_num}</p>
                <p> {room.max_guests - room.base_guests}</p>
                <p>{room.price_per_night.toLocaleString()}/</p>
                <p>
                  {stayDuration} :{" "}
                  {(room.price_per_night * stayDuration).toLocaleString()}
                </p>
                <div
                  className="reserv-btn"
                  onClick={() => handleReservation(room)}
                >
                  <span></span>
                </div>
              </div>
            </li>
          ))
        ) : (
          <p className="no-room">   .</p>
        )}
      </ul>
    </div>
  );
}

export default ReservRoom;
