import React, { useEffect, useRef, useState } from "react";

//   ///
import $ from "jquery";

//   
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

//  CSS  ////
import "../../css/pages/post.scss";

//   JS ////
import { initPostData } from "../../js/func/post_fn";
import List from "./../modules/PostModules/List";
import Read from "./../modules/PostModules/Read";
import Modify from "./../modules/PostModules/Modify";
import Write from "./../modules/PostModules/Write";

import { hotels } from "../../js/data/hotels";
import { users } from "../../js/data/users";

function Post() {
  //     !
  initPostData();

  // [   ! ] //////
  const posts = JSON.parse(localStorage.getItem("posts"));
  // const posts = JSON.parse(localStorage.getItem("posts"));

  // [    ] ///////////////
  // [1]  /////
  const [mode, setMode] = useState("L");
  // (1)  (L) : List Mode
  // (2)  (R) : Read Mode
  // (3)  (W) : Write Mode
  // (4)  (M) : Modify Mode ()

  // [2]     ////
  const [pageNum, setPageNum] = useState(1);

  // [3]   : (review) / (Q&A)
  const [type, setType] = useState("review");

  // [3]   :  {cta:,kw:}
  const [keyword, setKeyword] = useState({
    cta: "title",
    kw: "",
  });
  // console.log("{cta:,kw:}", keyword);
  // cta - creteria / kw -  keyword

  // [4]    : (asc(-1) / desc(1))
  const [order, setOrder] = useState(1);

  // [5]    :  ->  DB 
  const [sortCta, setSortCta] = useState("created_at");
  //    desc!
  // sortCta sort Criteria () !

  // [    ] //////
  // [1]    :  
  const selRecord = useRef(null);
  // -> / .current  !
  // console.log(" :", selRecord);

  // [2]   ( )
  // ->     !
  const totalCount = useRef(posts.length);
  // console.log(":", totalCount);

  // [3]   
  const pgPgNum = useRef(1);
  // ->    ?
  //    
  //   
  //  !    

  // [    ] //////
  const initVariables = () => {
    setMode("L");
    setPageNum(1);
    setKeyword({ cta: "title", kw: "" });
    setOrder(1);
    setSortCta("created_at");
    // selRecord.current = null;
    // totalCount.current = posts.length;
    pgPgNum.current = 1;
  }; ////////// initVariables  //////

  // [   :      ]
  // [1]   :  
  const unitSize = 10;
  // [2]    :    
  const pgPgSize = 3;

  //    
  const orgData = posts.filter((v) => v.post_type === type);

  // for       
  totalCount.current = orgData.length;
  // console.log(orgData);

  //  //
  //  [    ]  //
  //  //

  //    
  let finalData;

  // [      ] /////////////
  // [1]    ////////
  if (keyword.kw !== "") {
    finalData = orgData
      // ((1))-> sortCta  
      //  -1 * order 1 
      //  -1 * order -1 
      //
      .sort((a, b) =>
        a[sortCta] > b[sortCta] || a.id > b.id
          ? -1 * order
          : a[sortCta] < b[sortCta] || a.id < b.id
          ? 1 * order
          : //  :  ?
          a[sortCta] !== b[sortCta]
          ? //   0
            0
          : //    ?
          // id /
          a.id > b.id
          ? -1 * order
          : a[sortCta] < b[sortCta]
          ? 1 * order
          : 0
      )
      //    
      .filter((v) => {
        // console.log(keyword.cta);
        if (
          v[keyword.cta].toLowerCase().indexOf(keyword.kw.toLowerCase()) !== -1
        )
          return true;
      }); ////// filter ////////
  } ///// if :    /////////
  else {
    finalData = orgData
      // ((1))-> sortCta  
      //  -1 * order 1 
      //  -1 * order -1 
      //
      .sort((a, b) =>
        a[sortCta] > b[sortCta]
          ? -1 * order
          : a[sortCta] < b[sortCta]
          ? 1 * order
          : //  :  ?
          a[sortCta] !== b[sortCta]
          ? //   0
            0
          : //    ?
          // id /
          a.id > b.id
          ? -1 * order
          : a[sortCta] < b[sortCta]
          ? 1 * order
          : 0
      );
  } ///// else :    ////////

  //      /////
  totalCount.current = finalData.length;

  // [    ]
  // ->   10 
  // ->    
  // -> pageNum, unitSize  

  //   :  * (-1)
  let initNum = unitSize * (pageNum - 1);
  //   :  * 
  let limitNum = unitSize * pageNum;
  //  ( 5, 1~3)
  // (5*(1-1)) = 0 /  (5*1) = 5
  // (5*(2-1)) = 5 /  (5*2) = 10
  // (5*(3-1)) = 10 /  (5*3) = 15

  const selData = [];

  // [      ] ///
  for (let i = initNum; i < limitNum; i++) {
    if (i > totalCount.current - 1) break;
    // if (!posts[i]) break; //     

    //  ! ///
    // selData.push(posts[i]);

    // console.log(finalData[i].post_type, type);

    const user = users.find((u) => u.id === finalData[i].user_id);
    const hotel = hotels.find((h) => h.id === finalData[i].hotel_id);

    selData.push({
      id: finalData[i].id,
      user_name: user ? user.name : "  ",
      hotel_name: hotel ? hotel.name : "  ",
      post_type: finalData[i].post_type,
      rating: finalData[i].rating,
      title: finalData[i].title,
      content: finalData[i].content,
      created_at: finalData[i].created_at,
      user_id: finalData[i].user_id,
    });
  } //////////// for :  

  // console.log("slice  /", initNum, "/", limitNum);

  /************************************** 
     : searchFn
     :    
         !
  **************************************/
  const searchFn = () => {
    //      !
    // ->     !
    setKeyword({
      cta: $("#cta").val(),
      kw: $("#stxt").val(),
    });
  }; /////// searchFn 

  // DOM   ///////
  useEffect(() => {
    //    ///
    window.scrollTo(0, 0);
  }); //// useEffect /////////

  //   /////////////////
  return (
    <>
    <div className="post-cont">

      {
        // [1]    : mode -> "L" ////
        mode === "L" && (
          <List
          selData={selData} //   
          setMode={setMode} //   setter
          selRecord={selRecord} //  
          //    /////
          pageNum={pageNum} //   getter
          setPageNum={setPageNum} //   setter
          unitSize={unitSize} //  
          totalCount={totalCount} //   
          pgPgSize={pgPgSize} //   
          pgPgNum={pgPgNum} //   
          // ,     /////
          type={type} //   getter
          setType={setType} //   setter
          searchFn={searchFn} // 
          keyword={keyword} //   getter
          setKeyword={setKeyword} //   setter
          order={order} //  
          setOrder={setOrder} //   setter
          sortCta={sortCta} //   getter
          setSortCta={setSortCta} //   setter
          initVariables={initVariables} // 
          />
        )
      }

      {
        // [2]   : mode -> "R" ///
        mode === "R" && (
          <Read
          setMode={setMode} //   setter
          selRecord={selRecord} //  
          totalCount={totalCount} //   
          setPageNum={setPageNum} //   setter
          pgPgNum={pgPgNum} //   
          />
        )
      }

      {
        // [3]   : mode -> "W" ///
        mode === "W" && (
          <Write
          setMode={setMode} //   setter
          totalCount={totalCount} //   
          setPageNum={setPageNum} //   setter
          pgPgNum={pgPgNum} //   
          initVariables={initVariables} // 
          />
        )
      }

      {
        // [4]   : mode -> "M" ///
        mode === "M" && (
          <Modify
          setMode={setMode} //   setter
          selRecord={selRecord} //  
          totalCount={totalCount} //   
          setPageNum={setPageNum} //   setter
          pgPgNum={pgPgNum} //   
          />
        )
      }
      </div>
    </>
  );
}

export default Post;
